import React from 'react'
import { Route, HashRouter as Router, Switch } from 'react-router-dom'
import { Container, Nav, Navbar, Spinner,  } from 'react-bootstrap'


import { Home, LastEvents } from './pages'
import 'bootstrap/dist/css/bootstrap.min.css';

const PageNotFound = () => (
    <div>Page not found - 404</div>
)

const Header = () => (
  <Navbar bg="primary" variant="dark" expand="lg" fixed="top">
    <Navbar.Brand href="#/">KRDAE-HISTORY</Navbar.Brand>
    <Navbar.Toggle />
    <Navbar.Collapse>
      <Nav>
        <Nav.Link href="#/">Home</Nav.Link>
        <Nav.Link href="#lastevents">Last Events</Nav.Link>
      </Nav>
    </Navbar.Collapse>
      {/* TODO: Make loading status context management */}
      <Spinner className="justify-content-end" animation="border" role="status" variant="light" />
  </Navbar>
)

const Footer = () => (
  <Navbar bg="dark" variant='dark' fixed="bottom">
    <Navbar.Brand href="https://github.com/eozer/krdae-history">
      <div>
        <img src={`${process.env.PUBLIC_URL}/github-icon.png`} width={32} height={32} alt="Github icon" />{' '}
        eozer/krdae-history
      </div>
    </Navbar.Brand>
    <Navbar.Collapse className="justify-content-end">
      <Navbar.Text>LGPLv2.1 Licensed | Copyright © 2021-present <a href="https://github.com/eozer/">Ege Can Özer</a></Navbar.Text>
    </Navbar.Collapse>
  </Navbar>
)

const App = () => {

  return (
    <Container fluid>
      <Header />
      <div style={{ paddingTop: "4.5em" }}>
        <Router basename={process.env.PUBLIC_URL}>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/lastevents" component={LastEvents} />
            <Route component={PageNotFound} status={404} />
          </Switch>
        </Router>
      </div>
      <Footer />
    </Container>
  )
}

export default App;